.imageKit__container {
	min-height: 100%;
	border-radius: 2px;
	position: relative;

	> * {
		background-color: #d9d9d9;
		width: 80%;
		margin: 0 auto;
	}
}

.imageKit__animationContainer {
	display: flex;
	flex-direction: column;
	align-items: center;

	.imageKit__animation {
		width: clamp(200px, 80%, 320px);
		margin: 0 auto;
		height: auto;
	}

	p {
		font-size: 1rem;
		font-weight: 500;
	}
}

.imageKit__controls {
	padding: 0.5em 1em;
	display: flex;
	justify-content: space-between;

	p {
		font-weight: 500;
		font-size: 0.6rem;
	}

	#runModelBtn {
		background-color: transparent;
		border: none;
		cursor: pointer;

		// animation: load-rotate 2s infinite forwards;

		img {
			width: 40px;
		}

		&:hover {
			filter: drop-shadow(0 0 5px #222);
		}
	}
}

.imageKit__image {
	position: relative;
	display: flex;
	justify-content: center;

	#imageKit__imageBox {
		border-radius: 4px;
		width: (200px, auto, 100%);
		margin: 1em 2em;
		min-height: 100%;
		overflow: hidden;

		img {
			object-fit: cover;
			width: 100%;
			height: 100%;
		}
	}
}

.imageKit__upload {
	padding: 2em;

	#uploadImageBtn {
		display: inline-block;
		text-align: center;
		width: 100%;
		background-color: #ffffff;
		text-transform: capitalize;
		padding: 1em 2em;
		cursor: pointer;
		border: none;
		border-radius: 4px;
		font-weight: 500;
		font-size: 1rem;
		font-style: inherit;
		box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px,
			rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
		transition: all 100ms ease-in-out;

		input {
			display: none;
		}

		&:hover {
			background-color: #f0eeee;
		}
	}
}

.zoomControl__container {
	display: flex;
	align-items: center;
	justify-content: space-around;
	position: absolute;
	background-color: transparent;
	top: 105%;
	left: 0;
	width: 100%;

	button {
		background-color: transparent;
		border: none;
		font-family: inherit;
		cursor: pointer;
		font-size: 0.8rem;
		transition: all 100ms ease-in-out;

		&:hover {
			font-weight: bold;
		}
	}
}

@keyframes load-rotate {
	0% {
		transform: rotate(0deg);
	}
	25% {
		transform: rotate(90deg);
	}
	50% {
		transform: rotate(80deg);
	}
	75% {
		transform: rotate(170deg);
	}
	100% {
		transform: rotate(160deg);
	}
}
